import * as React from 'react';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import { menuHeight } from './layout';
import { useStaticQuery, graphql } from 'gatsby';

interface IMainvisualImages {
  pc: string;
  sp: string;
}

interface IMainvisual {
  isLoaded: boolean;
  images: IMainvisualImages[];
  messages: React.ReactNode[];
}

const showingTime: number = 3000;
const duration: number = 1000;

const sleep = (ms: number) => new Promise(resolve => setTimeout(resolve, ms));

export const Mainvisual = ({ isLoaded, images, messages }: IMainvisual) => {
  const [currentImageIndex, setCurrentImageIndex] = React.useState<number>(0);
  const [isShow, setIsShow] = React.useState<boolean>(false);
  const [isSp, setIsSp] = React.useState<boolean>(false);

  React.useEffect(() => {
    const onResize = () => {
      setIsSp(
        document.documentElement.clientWidth <
          document.documentElement.clientHeight
      );
    };

    onResize();
    window.addEventListener('load', onResize);
    window.addEventListener('resize', onResize);

    return () => {
      window.removeEventListener('resize', onResize);
    };
  }, []);

  React.useEffect(() => {
    const f = async () => {
      setIsShow(true);
      await sleep(duration);
      await sleep(showingTime);
      setIsShow(false);
      await sleep(duration);
      setCurrentImageIndex(i => (i + 1) % images.length);
    };

    let timer: NodeJS.Timeout;

    if (images.length > 1) {
      timer = setInterval(f, duration + showingTime + duration);
      f();
    } else {
      setIsShow(true);
    }

    return () => {
      clearTimeout(timer);
    };
  }, []);

  const data = useStaticQuery(graphql`
    query {
      allFile {
        nodes {
          relativePath
          childImageSharp {
            gatsbyImageData(width: 1920, layout: FIXED)
          }
        }
      }
    }
  `);

  const imageSharpPc = data.allFile.nodes.find(
    ({ relativePath }: { relativePath: string }) =>
      relativePath === images[currentImageIndex].pc
  );

  const imageSharpSp = data.allFile.nodes.find(
    ({ relativePath }: { relativePath: string }) =>
      relativePath === images[currentImageIndex].sp
  );

  if (!imageSharpPc || !getImage(imageSharpPc)) return <></>;
  const imageDataPc: any = getImage(imageSharpPc);

  if (!imageSharpSp || !getImage(imageSharpSp)) return <></>;
  const imageDataSp: any = getImage(imageSharpSp);

  return (
    <Box
      sx={{
        position: 'relative',
        marginTop: `${menuHeight * -1}px`,
        transition: 'height 250ms ease-out',
        height: '100vh',
        borderRadius: '5dvw',
        overflow: 'hidden'
        // @TODO
        // eslint-disable-next-line no-dupe-keys
        // height: '100dvh'
      }}
    >
      <Box
        sx={{
          position: 'absolute',
          width: '100%',
          height: '100%',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          zIndex: 9
        }}
      >
        <Box
          sx={{
            position: 'absolute',
            width: '100dvw',
            height: '100dvh',
            overflow: 'hidden',
            backgroundColor: 'white'
          }}
        >
          <Box
            sx={{
              position: 'absolute',
              width: '100%',
              height: '100%',
              // backgroundColor: 'rgba(0,0,0,0.25)',
              zIndex: 99
            }}
          />
          <GatsbyImage
            image={isSp ? imageDataSp : imageDataPc}
            alt='main'
            objectPosition='50% 0%'
            style={{
              width: '100%',
              height: '100%',
              opacity: isShow ? 1 : 0,
              transition: `all ${duration}ms ease-out`
            }}
          />
        </Box>
        <Container>
          <Box
            sx={{
              display: 'flex',
              height: '100dvh',
              justifyContent: 'flex-end',
              mb: 10,
              alignItems: 'flex-start',
              flexDirection: 'column'
              /*
              mb: {
                md: 28,
                sm: 20,
                xs: 20
              }
              */
            }}
          >
            {messages?.map((label, i) => {
              const duration = 1000;
              const delay = i * 500 + 1000;

              return (
                <Box
                  key={i}
                  sx={{
                    zIndex: 999,
                    transition: [
                      `opacity ${duration}ms ease-out ${delay}ms`,
                      `transform ${duration}ms ease-out ${delay}ms`
                    ].join(','),
                    opacity: isLoaded ? 1 : 0,
                    transform: `translateY(${isLoaded ? 0 : 100}px)`
                  }}
                >
                  <Typography
                    // color='primary'
                    color='white'
                    sx={{
                      display: 'block',
                      fontSize: {
                        lg: 80,
                        md: 60,
                        sm: 40,
                        xs: 28
                      },
                      //background: 'linear-gradient(to right, rgba(255,255,255,0.0), rgba(255,255,255,0.8))',
                      // textShadow: '1px 1px 4px #555',
                      textShadow: '1px 1px 4px #e8827a',
                      fontWeight: 300,
                      letterSpacing: '0.1em',
                      lineHeight: 1.5
                      // bgcolor: 'background.default',
                      // bgcolor: 'rgba(255,255,255,0.8)',
                    }}
                  >
                    {label}
                  </Typography>
                </Box>
              );
            })}
          </Box>
        </Container>
      </Box>
    </Box>
  );
};
